@import 'src/scss/settings/mixins';

.rodo-component {
  width: 95%;
  position: absolute;
  bottom: 1rem;
  left: 2rem;
  align-self: center;
  font-size: 1.2rem;
  animation-duration: 0.5s;
  animation-name: rodoAnimationBack;
  display: none;
  overflow: hidden;
}

@keyframes rodoAnimationBack {
  0% {
    display: none;
    opacity: 0;
  }
  99% {
    display: none;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
.active-rodo {
  display: block;
  animation-name: rodoAnimation forwards;
}
@keyframes rodoAnimation {
  0% {
    display: none;
    opacity: 0;
  }
  99% {
    display: none;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}

@include tabletMax {
  .rodo-component {
    font-size: 0.8rem;
  }
}

@include mobile {
  .rodo-component {
    font-size: 0.6rem;
  }
}
